import Dexie from "dexie";
import { AppInsightsService } from './appInsightsService';

export class DbServiceClass
{
  db: Dexie;
  disabled = false;
  constructor()
  {
    try
    {
      this.db = new Dexie("mFormatterDb");
      this.db.version(1).stores({
        std: "options"
      });
      this.db.version(2).stores({
        state: "name, object"
      });
    }
    catch(err)
    {
      this.disabled = true; 
    }
  }
  
  public async store(name: string, options: any)
  {
    if(this.disabled == true)
      return;
    try
    {
      await this.db.table("state").put({
        name,
        object: JSON.stringify(options)
      })
    }
    catch(err)
    {
      AppInsightsService.trackError("Error in dbService store()", err);
    }
  }
  
  public async get<T>(name: string)
  {
    if(this.disabled == true)
      return;
    try 
    {
      var res = await this.db.table("state").where("name").equals(name).toArray();
      if(res.length == 0)
        return null;
      return JSON.parse(res[0].object) as T;
    } 
    catch (error) 
    {
      AppInsightsService.trackError("Error in dbService get()", error);
      return null;
    }
  }
  
  public async del(name: string)
  {
    if(this.disabled == true)
    return;
  try 
  {
    await this.db.table("state").where("name").equals(name).delete();
  } 
  catch (error) 
  {
    AppInsightsService.trackError("Error in dbService del()", error);
    return null;
  }
  }
}

export const DbService = new DbServiceClass();