import React from "react";
import { OutboundLink } from "gatsby-plugin-google-analytics";
import { StateService } from '../services/stateService';

type Issue = {
  title: string;
  url: string;
  html_url: string;
}

export class Issues extends React.Component<{}, {issues: Issue[]}>
{
  constructor(props)
  {
    super(props);
    this.state = {
      issues: null
    };
  }
  
  async componentDidMount()
  {
    try 
    {
      if(typeof window !== "undefined")
      {
        let state = await StateService.retrieveState<{issues: Issue[]}>("issues");
        let issues = state?.issues
        if(issues == null)
        {
          fetch(`https://api.github.com/repos/mogulargmbh/m-formatter/issues`).then(r => r.json().then(e => {
            StateService.storeState("issues", {issues: e}, false);
            this.setState({
              issues: e
            })
          }));
        }
        else
        {
          this.setState({
            issues
          })
        }
      }
    } 
    catch (error) 
    {
      console.error(error);  
    }
  }
  
  render()
  {
    return (
      <div>
        <h1><OutboundLink href="https://github.com/mogulargmbh/m-formatter/issues" target="_blank">Open Issues</OutboundLink></h1>
        <div>
          {
            this.state.issues == null ?  <div className="loader"></div> : 
              this.state.issues.length == 0 ? <div>No issues</div> : 
                <ul>
                  {
                    this.state.issues.map((issue, i) => {
                      return (
                        <li key={i}><OutboundLink target="_blank" href={issue.html_url}>{issue.title}</OutboundLink></li>
                      )
                    })
                  }
                </ul>
          }
        </div>
      </div>
    )
  }
}