import React from "react";
import { Layout, LayoutWithSidebar } from '../components/Layout';
import { IHeaderProps } from '../components/Header';
import { FaBug } from "react-icons/fa";
import { Issues } from '../components/Issues';

export class BugReportPage extends React.Component
{
  render()
  {
    let header: IHeaderProps = {
      url: "/",
      type: "website",
      title: "Power Query Formatter | Beautify your PowerQuery code",
      description: "Format your M (PowerQuery) code with M Formatter"
    };
    return (
      <LayoutWithSidebar header={header}>
        <div>
          <h1><FaBug style={{position: "relative", top: ".15em"}} /> Report a bug</h1>
        </div>
        <div>
          Thanks for using Power Query Formatter. We appreciate reports about any bug that you encounter to improve our formatter service.
          Please use our <a href="https://github.com/mogulargmbh/m-formatter/issues/new/choose">bug report template</a> at our <a href="https://github.com/mogulargmbh/m-formatter">github repository</a>. Thanks!
        </div>
        <div style={{marginTop: "10px"}}>
          <Issues></Issues>
        </div>
      </LayoutWithSidebar>
    );
  }
}

export default BugReportPage;